<template>
   <div>
       <Head  title="商品列表" />
<van-tabs v-model="active">
  <van-tab :title="a.text" v-for="(a,i) in typelist" :key="i" :name="a.value">
  <List2 :goods="goodslist.filter(ele=>ele.type.value==a.value)"/>     
  </van-tab>
</van-tabs>
   </div>
</template>


<style lang="scss" scoped>
:root{
    background-color:#cdcdcd  !important;
}

.van-tab--active{
   color: #ff464e !important; 
} 


</style>

<script>

export default {
  
   data(){
      return {
         typelist:[],
         goodslist:[],
         active:0
      }
   },
   mounted(){
      this.$axios.get("/vue/getGoodTypes").then(res=>{
      this.typelist=res.data.result;
      })
   },
   created(){
        this.$axios.get("/vue/getGoods").then(res=>{
      this.goodslist=res.data.result;
     // window.console.log( this.goodslist);
      })
   }
   
}
</script>